<template>
<section class="center-players">
  <div class="container mx-auto p-1">
    <div class="w-full h-auto bg-white p-1">
      <slot name="players"><p class="font-bold text-black font-black w-full">بازیکنان تیم ایران</p></slot>
      <div class="bg-black w-full h-0.5 my-1"></div>
      <div class="w-full min-h-screen p-2 relative bg-green-600">
        <example-goal-keeper :my-list="list">
          <template #goalKeeper>
            <p class="font-bold text-white font-black w-full">دروازه بان های تیم ایران</p>
          </template>
        </example-goal-keeper>
        <defender-irans></defender-irans>
        <after-atteck></after-atteck>
        <attack-iran></attack-iran>
        <img :src="require('../../../assets/image/squad-middle.png')" alt="" class="h-auto absolute mx-auto bottom-2.5 w-full left-0 right-0">
      </div>
    </div>
  </div>
</section>
</template>

<script>
import {reactive,onMounted} from 'vue'
import axios from 'axios'
import {handleError} from "@/components/composable/handleError";
import DefenderIrans from "@/components/DefenderIrans";
import AfterAtteck from "@/components/AfterAtteck";
import AttackIran from "@/components/AttackIran";
import ExampleGoalKeeper from "@/components/main/ExampleGoalKeeper";
export default {
  name: "PlayersOfIrans",
  components: {ExampleGoalKeeper, AttackIran, AfterAtteck, DefenderIrans,},
  setup(){
    const list=reactive([])
    const result=onMounted(async()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/6388'),
         axios.get('https://api.footballi.net/api/v2/player/6450'),
         axios.get('https://api.footballi.net/api/v2/player/6499'),
         axios.get('https://api.footballi.net/api/v2/player/2226218'),
      ])
         .then(axios.spread((...response)=>{
             list.push(response)
         }))
      .catch((error)=>{
        handleError(error)
      })

    })

    return{
      list,
      result,
    }
  }
}
</script>

<style scoped>
.one{
  z-index: 999;
}
</style>