<template>
  <section id="scroll" class=" scroll center-final-table border border-1 rounded-sm border-slate-300 p-1" :style="{height:scrollHeight+'px'}">
    <div class="container mx-auto p-2">
      <div class="w-full h-auto">
        <div class="flex flex-col space-y-2">
          <slot name="teamPlay"><p class="font-semibold text-sm text-slate-900 text-right">برنامه بازی های ایران</p></slot>
          <div class="bg-slate-900 w-full h-0.5 my-3"></div>
          <div class="border border-1 border-slate-400 rounded-sm mx-auto align-center w-4/12 p-2 relative">
            <div class="flex flex-row w-full justify-between">
              <p class="text-slate-800">{{myVal}}</p>
                <font-awesome-icon @click="flag=!flag" :icon="['fas','angle-down']" class="text-black" :style="{fontSize:size+'px'}"></font-awesome-icon>
            </div>
              <div v-show="flag" class="absolute bg-white border border-1 border-slate-300 rounded-sm p-2 text-center" style="width: 130px;top: 36px;left: -2px">
                <div v-for="items in menu" :key="items" class="flex flex-row justify-center my-3">
                  <div @click="getInfo(items.nums,items.title,items.id)">
                  <span class="text-slate-800" :style="{fontSize:size+'px'}">{{items.title}}</span>
                  <span class="text-slate-800" :style="{fontSize:size+'px'}">{{items.nums}}</span>
                  </div>
                </div>
              </div>
          </div>

            <div v-for="items in list" :key="items">
              <div v-for="value in items.matches" :key="value" class="my-3">
                <div class="flex flex-col space-y-2">
                  <div class="flex flex-row my-2">
                    <span class="text-center font-thin text-slate-600" :style="{size:sizeText+'px'}">{{value.date_text}}</span>
                    <span class="text-center font-thin text-slate-600" :style="{size:sizeText+'px'}">{{value.week}}</span>
                  </div>
                </div>
                <div class="flex flex-row justify-between w-full">
                  <div class=" w-4/12 flex flex-row">
                    <span class="text-center font-semibold text-slate-900 text-sm">{{value.home_team.name_fa}}</span>
                    <img :src="`${value.home_team.logo}`" alt="" class="w-5 h-5 rounded-full mx-1">
                  </div>
                  <div class=" w-4/12 flex flex-col">
                    <div class="flex flex-row">
                      <span class="text-center font-semibold text-slate-900 text-sm">{{value.home_team_score}}</span>
                      <span>-</span>
                      <span class="text-center font-semibold text-slate-900 text-sm">{{value.away_team_score}}</span>
                    </div>
                    <span class="text-center font-semibold text-slate-900 text-sm">{{value.status}}</span>
                  </div>
                  <div class=" w-4/12 flex flex-row">
                    <img @error="notShow" :src="`${value.away_team.logo}`" alt="" class="w-5 h-5 rounded-full mx-1">
                    <span class="text-center font-semibold text-slate-900 text-sm">{{value.away_team.name_fa}}</span>
                  </div>
                </div>

              </div>
            </div>

      </div>
    </div>
    </div>
  </section>
</template>

<script>
import {reactive, ref,onMounted} from "vue";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import axios from 'axios'
import {handleError} from "@/components/composable/handleError";
export default {
  name: "ExampleTeamPlay",
  components: {FontAwesomeIcon},

  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    const indexes=ref(0)
    const list=reactive([])
    const myVal=ref('خرداد 1403')
    const flag=ref(false)
    const size=ref(13)
    const sizeText=ref(10)
    const scrollHeight=ref(540)
   const menu=reactive([
     {id:0,title:"خرداد",nums:1403},
     {id:1,title:"فروردین",nums:1403},
     {id:2,title:"ابان",nums:1402},
     {id:3,title:"مهر",nums:1402},
     {id:4,title:"شهریور",nums:1402},
     {id:5,title:"تیر",nums:1402},
     {id:6,title:"فروردین",nums:1402},
   ])
    const show=onMounted(()=>{
      showMatches()
    })
    function getInfo(data,nums,index){
      myVal.value=`${nums}` + `${data}`
      indexes.value=index
      flag.value=false
      list.shift()
      showMatches()
    }

    function showMatches(){
      axios.get('https://api.footballi.net/api/v2/team/1032/main')
      .then((response)=>{
        if(response.data.success){
         list.push(response.data.data.matches[indexes.value])
        }
      })
      .catch((error)=>{
       handleError(error)
      })
    }
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
     sizeText,scrollHeight,menu,size,flag,myVal,getInfo,list,showMatches,indexes,show,notFound,notShow
    }
  }
}
</script>

<style scoped>
.scroll{
  height:auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: grey;
}
#scroll::-webkit-scrollbar-track{
  background-color: #fff;
  -webkit-box-shadow: inset 0 0 0 6px rgba(0,0,0,0.3);
}
</style>