<template>
<example-defender :my-list="menu">

</example-defender>
</template>

<script>
import {reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleDefender from "@/components/main/ExampleDefender";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "DefenderIrans",
  components:{ExampleDefender},
  setup(){
    const menu=reactive([])
    const result=onMounted(async ()=>{
    await axios.all([
       axios.get('https://api.footballi.net/api/v2/player/1580'),
       axios.get('https://api.footballi.net/api/v2/player/1635'),
       axios.get('https://api.footballi.net/api/v2/player/1666'),
       axios.get('https://api.footballi.net/api/v2/player/4726'),
       axios.get('https://api.footballi.net/api/v2/player/6251'),
       axios.get('https://api.footballi.net/api/v2/player/6392'),
       axios.get('https://api.footballi.net/api/v2/player/6399'),
       axios.get('https://api.footballi.net/api/v2/player/6515'),
       axios.get('https://api.footballi.net/api/v2/player/9207'),
       axios.get('https://api.footballi.net/api/v2/player/2258214'),
     ])
      .then(axios.spread((...response)=>{
        menu.push(response)
      }))
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      menu,
      result
    }
  }
}
</script>

