<template>
<example-main-attack :my-list="list">

</example-main-attack>
</template>

<script>
import{reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleMainAttack from "@/components/main/ExampleMainAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AttackIran",
  components:{ExampleMainAttack},
  setup(){
    const list=reactive([])
    const sum=onMounted(async()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/5330'),
        axios.get('https://api.footballi.net/api/v2/player/6337'),
        axios.get('https://api.footballi.net/api/v2/player/6385'),
        axios.get('https://api.footballi.net/api/v2/player/9211'),
      ])
      .then(axios.spread((...res)=>{
        list.push(res)
      }))
      .catch((error)=>{
       handleError(error)
      })

    })
    return{
      list,
      sum,
    }
  }
}
</script>

