<template>
<example-attack :my-list="list">

</example-attack>
</template>

<script>
import{reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AfterAtteck",
  components:{ExampleAttack},
  setup(){
    const list=reactive([]);
    const total=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/4016'),
        axios.get('https://api.footballi.net/api/v2/player/4811'),
        axios.get('https://api.footballi.net/api/v2/player/6285'),
        axios.get('https://api.footballi.net/api/v2/player/6351'),
        axios.get('https://api.footballi.net/api/v2/player/21202'),
        axios.get('https://api.footballi.net/api/v2/player/30055'),
      ])
      .then(axios.spread((...res)=>{
        list.push(res)
      }))
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      list,
      total,
    }
  }
}
</script>

