<template>
<example-team-news :my-list="menu">

</example-team-news>
</template>

<script>
import{reactive,onMounted,ref} from 'vue'
import EndPoints from "@/service/EndPoints";
import ExampleTeamNews from "@/components/main/ExampleTeamNews";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "LastNewsIran",
  components:{ExampleTeamNews},
  setup(){
    const heightBox=ref(850)
    const heightImg=ref(500)
    const menu=reactive([]);
    const sum=onMounted(()=>{
      EndPoints.getNewsIran()
      .then((response)=>{
        if(response.data.success){
          menu.push(response.data.data)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      heightImg,
      heightBox,
      menu,
      sum,
    }
  }
}
</script>

